"use client";
import { useState } from "react";
import Link from "next/link";
import AuthModal from "./AuthModal";
import { motion } from "framer-motion";

export default function Navbar() {
  const [open, setOpen] = useState(false);
  const [showAuth, setShowAuth] = useState(false);

  return (
    <>
      {/* NAVBAR */}
      <nav className="bg-gray-900/80 backdrop-blur-lg border-b border-gray-800 fixed top-0 left-0 w-full z-50">
        <div className="max-w-7xl mx-auto px-6 py-4 flex justify-between items-center">

          {/* LOGO */}
          <Link href="/">
            <motion.h1
              initial={{ opacity: 0, y: -10 }}
              animate={{ opacity: 1, y: 0 }}
              className="text-2xl font-bold text-blue-400 cursor-pointer"
            >
              VividSeats Clone
            </motion.h1>
          </Link>

          {/* DESKTOP MENU */}
          <div className="hidden md:flex items-center gap-8">
            <Link href="/" className="text-gray-300 hover:text-white">Home</Link>
            <Link href="/events" className="text-gray-300 hover:text-white">Events</Link>
            <Link href="/teams" className="text-gray-300 hover:text-white">Teams</Link>

            {/* LOGIN BUTTON */}
            <button
              onClick={() => setShowAuth(true)}
              className="bg-blue-600 px-4 py-2 rounded-lg hover:bg-blue-500 transition"
            >
              Login
            </button>
          </div>

          {/* MOBILE MENU TOGGLE */}
          <button
            className="md:hidden text-gray-300 text-3xl"
            onClick={() => setOpen(!open)}
          >
            {open ? "✕" : "☰"}
          </button>
        </div>

        {/* MOBILE MENU ITEMS */}
        {open && (
          <div className="md:hidden px-6 pb-4 space-y-4 bg-gray-900 border-t border-gray-800">
            <Link
              href="/"
              className="block text-gray-300 hover:text-white"
              onClick={() => setOpen(false)}
            >
              Home
            </Link>

            <Link
              href="/events"
              className="block text-gray-300 hover:text-white"
              onClick={() => setOpen(false)}
            >
              Events
            </Link>

            <Link
              href="/teams"
              className="block text-gray-300 hover:text-white"
              onClick={() => setOpen(false)}
            >
              Teams
            </Link>

            {/* MOBILE LOGIN BUTTON */}
            <button
              onClick={() => {
                setOpen(false);
                setShowAuth(true);
              }}
              className="w-full bg-blue-600 py-2 rounded-lg hover:bg-blue-500 transition"
            >
              Login
            </button>
          </div>
        )}
      </nav>

      {/* AUTH MODAL */}
      {showAuth && <AuthModal onClose={() => setShowAuth(false)} />}
    </>
  );
}
